import styled from "styled-components";

export default styled.span`
color: #aaa;
float: right;
font-size: 3rem;
font-weight: bold;
position:relative;
top:-2rem;
right:-1rem;

&:hover,
&:focus {
color: black;
text-decoration: none;
cursor: pointer;

@media (min-width: 660px) {

}
  `;



